'use strict';

// MODULES //

var bytesPerElement = require( '@stdlib/ndarray-base-bytes-per-element' );
var defaults = require( './defaults.js' );


// MAIN //

/**
* Returns a loop block size for multi-dimensional array tiled loops.
*
* @param {*} dtypeX - first input array data type
* @param {*} dtypeY - second input array data type
* @param {*} dtypeZ - third input array data type
* @param {*} dtypeW - fourth input array data type
* @param {*} dtypeU - fifth input array data type
* @param {*} dtypeV - output array data type
* @returns {integer} block size (in units of elements)
*
* @example
* var bsize = quinaryBlockSize( 'float64', 'float64', 'float64', 'float64', 'float64', 'float64' );
* // returns <number>
*/
function quinaryBlockSize( dtypeX, dtypeY, dtypeZ, dtypeW, dtypeU, dtypeV ) {
	var nbx;
	var nby;
	var nbz;
	var nbw;
	var nbu;
	var nbv;
	var max;

	nbx = bytesPerElement( dtypeX );
	nby = bytesPerElement( dtypeY );
	nbz = bytesPerElement( dtypeZ );
	nbw = bytesPerElement( dtypeW );
	nbu = bytesPerElement( dtypeU );
	nbv = bytesPerElement( dtypeV );
	if (
		nbx === null ||
		nby === null ||
		nbz === null ||
		nbw === null ||
		nbu === null ||
		nbv === null
	) {
		// e.g., "generic" arrays
		return defaults.BLOCK_SIZE_IN_ELEMENTS;
	}
	// Find the largest element size among all six arrays...
	max = nbx;
	if ( nby > max ) {
		max = nby;
	}
	if ( nbz > max ) {
		max = nbz;
	}
	if ( nbw > max ) {
		max = nbw;
	}
	if ( nbu > max ) {
		max = nbu;
	}
	if ( nbv > max ) {
		max = nbv;
	}
	return ( defaults.BLOCK_SIZE_IN_BYTES/max )|0; // asm type annotation
}


// EXPORTS //

module.exports = quinaryBlockSize;
